.ProjectItemContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    background-color: #0080FF;
    border: 1px solid;
}

.image {
    width: 111px;
    height: 111px;
    background-color: #f18d8d;
}

.projectInfo {
    background-color: #DDEEFF;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;

}

.projectTitle {
    border: 1px solid;
}

.description {
    border: 1px solid;

}